import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			navBar: navBar,
			is_bind: '',
			orderId: '',
			placeRecharge: '',
			count: 0
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		order() {
			this.leftClick()
		},
		/**
		 * 充值信息
		 */
		placeRechargea() {
			var that = this
			var id = that.orderId;







			var type = 'mini';

			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				id: id,
				type: type,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				id: id,
				type: type,

				pay: 'alipay',

				timeStamp: timeStamp,
				sign: sign,
			}
			rs.postRequests("placeRecharge", data, (res) => {
				if (res.data.code == 200) {
					that.placeRecharge = res.data.data
				} else if (res.data.code == 406) {
					rs.Toast('请先绑定微信')
				} else {
					rs.Toast(res.data.msg)
				}
			})
		},












































































































		querenchongzhi() {
			console.log('支付宝支付')
			var that = this;
			uni.requestPayment({
				provider: 'alipay',
				orderInfo: that.placeRecharge.alipayParams.trade_no, //微信、支付宝订单数据	
				success: function(res) {
					console.log(res)

					if (res.resultCode == 9000) {
						rs.Toast('充值成功');
						setTimeout(function() {
							uni.switchTab({
								url: "/pages/tabar/user"
							})
						}, 1000);
					}

				},
				fail: function(err) {
					console.log(err)
					rs.Toast("充值失败");
				}

			})
		},


	},
	onLoad(options) {
		this.orderId = options.orderId
	},
	onShow() {
		var that = this;
		that.placeRechargea();
	}
};
